import React from "react";
import { connect } from "react-redux";
import "./style.css";

class Result extends React.Component {
  state = { clicked: 0 };

  fillPhoto = () => {
    try {
      const src = this.props.searchresult.state.items[this.state.clicked].pagemap
        .cse_thumbnail[0].src;

      return <img alt="imgm" id="imgm" src={src} />;
    } catch (err) {
      return <img alt="imgm" id="imgm" src="./7.jpg" />;
    }
  };

  fillResults = () => {
    try {
      document.getElementById("next").style.display = "flex";
      document.getElementById("prev").style.display = "flex";
      document.getElementById("or").style.display = "flex";
      return (
        <div id="result">
          <div id="card" className="ui card">
            <div className="image" id="photo">
              {this.fillPhoto()}
            </div>
            <div className="content">
              <a
                href={
                  this.props.searchresult.state.items[this.state.clicked]
                    .formattedUrl
                }
                className="header"
              >
                {this.props.searchresult.state.items[this.state.clicked].title}
              </a>

              <div className="description">
                {
                  this.props.searchresult.state.items[this.state.clicked]
                    .snippet
                }
              </div>
            </div>
          </div>
        </div>
      );
    } catch (err) {
      return "";
    }
  };

  next = () => {
    try {
      if (this.state.clicked + 1 < this.props.searchresult.state.items.length) {
        this.setState({ clicked: this.state.clicked + 1 });
      }
    } catch (err) {}
  };

  prev = () => {
    try {
      if (this.state.clicked - 1 >= 0) {
        this.setState({ clicked: this.state.clicked - 1 });
      }
    } catch (err) {}
  };

  render() {
    return (
      <div className="result">
        <div id="buttons">
          <div className="ui buttons" id="uibuttons">
            <button className="ui button" id="prev" onClick={this.prev}>
              poprzedni
            </button>
            <div className="or" id="or"></div>
            <button
              className="ui positive button"
              id="next"
              onClick={this.next}
            >
              następny
            </button>
          </div>
        </div>
        {this.fillResults()}
      </div>
    );
  }
}
const mapStateToProps = state => {
  return {
    searchresult: state.searchresult
  };
};

export default connect(
  mapStateToProps,
  {}
)(Result);
